                document.querySelector('.depth1:nth-child(3)>.depth1Title').classList.add('depth1TitleHover')
            } else if (e === 4) {
                document.querySelector('.depth1:nth-child(4)>.depth1Title').classList.add('depth1TitleHover')
            } else if (e === 5) {
